const tools = {
  /**
  * : 
  * isFileNameValid: 
  * @param {string} fileName 
  * @return {boolean | string} true, string 
  */
  isFileNameValid: (fileName) => {
    // 
    if (typeof fileName !== 'string') {
      console.error(`string: ${fileName}`);
      return 'string';
    }
    // 
    if (fileName.length < 1) {
      return '';
    }
    // 200
    if (fileName.length > 200) {
      return '200';
    }
    // 
    // 
    if (fileName.match(/^\d+$/)) {
      return '';
    }
    // 
    // eslint-disable-next-line no-restricted-syntax
    for (const char of fileName) {
      if (char.codePointAt(0) > 0xFFFF) return '';
    }

    //  . , tab,  
    if (fileName[0] === '.'
      || fileName[fileName.length - 1] === '.'
      || fileName[0] === ' '
      || fileName[fileName.length - 1] === ' '
      || fileName[0] === '\t'
      || fileName[fileName.length - 1] === '\t'
      || fileName[0] === '\n'
      || fileName[fileName.length - 1] === '\n'
    ) {
      return ' .  tab  ';
    }

    //  < > : " / \ | ? *
    const r = /[<>:"/\\|?*]/;
    if (fileName.match(r)) {
      return ' < > : " / \\ | ? * ';
    }

    // ____
    const trimedFileName = fileName.trim();
    if (trimedFileName[0] === '_' &&
      trimedFileName[1] === '_' &&
      trimedFileName[trimedFileName.length - 1] === '_' &&
      trimedFileName[trimedFileName.length - 2] === '_') {
      return '____';
    }
    return true;
  },
};

export default tools;
