/**
 * UserConfiggetset, default-configuser-config
 * getmerged-config
 */
import axios from 'axios';
import config from '@/config';

const { serverUrl } = config.server;

const UserConfig = {
  /**
  * getUserConfig 
  * @param {array} 
  * @param {function} , 
  * @return {object} 
  */
  async get(props, messager) {
    let userConfig = null;

    await axios.get(`${serverUrl}user-config`, {
      params: {
        props,
      },
    })
      .then((res) => {
        if (res.status === 200) {
          userConfig = res.data;
        } else {
          if (messager) {
            messager.error(`getUserConfig(): Bad HTTP status: ${res}`);
          }
          throw new Error(`getUserConfig(): Bad HTTP status: ${res}`);
        }
      })
      .catch((err) => {
        if (messager) {
          messager.error(`getUserConfig failed: ${err}`);
        }
        throw new Error(`getUserConfig failed: ${err}`);
      });
    return userConfig;
  },
};

export default UserConfig;
